import { GetTestOrderData, ImagingActionTypes } from '../actions';
import {
  Action,
  ActionReducerMap,
  createFeatureSelector,
  createSelector,
  combineReducers
} from '@ngrx/store';
import * as fromTestOrders from './test-orders.reducers';
import * as fromEmployee from './employee.reducer';
import * as fromSaveSelectedTestOrder from './selected-test.reducer';
import * as fromSaveFilterState from './filter-state.reducer';
import * as fromTestImagingPath from './test-image-paths.reducer';
import * as fromTestTemplates from './test-templates.reducer';
import * as fromGivenBy from './given-by-reducer';
import * as fromRouteList from './drug-routes.reducer';
import * as fromChartData from './imaging-chart-data.reducer';
import * as fromTemplateData from './template-form-data.reducer';
import { ImagingState } from '@app/modules/imaging/models';

export const reducers: ActionReducerMap<ImagingState> = {
  employees: fromEmployee.reducer,
  testOrders: fromTestOrders.reducer,
  selectedTestOrder: fromSaveSelectedTestOrder.reducer,
  filterState: fromSaveFilterState.reducer,
  selectedTestImagePaths: fromTestImagingPath.reducer,
  selectedTestTemplate: fromTestTemplates.reducer,
  givenByList: fromGivenBy.reducer,
  drugRouteList: fromRouteList.reducer,
  imagingChartData: fromChartData.reducer,
  selectedTemplateFormData: fromTemplateData.reducer
};

export const getImagingState = createFeatureSelector<ImagingState>(
  'IMAGING_FEATURE'
);

export const getDoctors = createSelector(getImagingState, state =>
  state.employees.filter(employee => employee.designation === 'Doctor')
);

export const getTechnicians = createSelector(getImagingState, state =>
  state.employees.filter(employee => employee.designation === 'Technician')
);

export const getSelectedTestOrder = createSelector(
  getImagingState,
  state => state.selectedTestOrder
);

export const getTestOrders = createSelector(
  getImagingState,
  state => state.testOrders
);

export const getFilterFormState = createSelector(
  getImagingState,
  state => state.filterState
);

export const getJpegImages = createSelector(getImagingState, state => {
  return {
    isLoading: state.selectedTestImagePaths.isLoading,
    imagePaths: state.selectedTestImagePaths.imagePaths
      .filter(val => val.jpegImagePath)
      .map(val => val.jpegImagePath),
    errorOccurred: state.selectedTestImagePaths.errorOccurred
  };
});

export const getDicomImges = createSelector(getImagingState, state => {
  return {
    isLoading: state.selectedTestImagePaths.isLoading,
    imagePaths: state.selectedTestImagePaths.imagePaths.filter(
      val => val.dicomImagePath
    ).map(val => val.dicomImagePath),
    errorOccurred: state.selectedTestImagePaths.errorOccurred
  };
});

export const getSelectedTestTemplate = createSelector(
  getImagingState,
  state => state.selectedTestTemplate
);

export const getGivenByList = createSelector(
  getImagingState,
  state => state.givenByList
);

export const getDrugRouteList = createSelector(
  getImagingState,
  state => state.drugRouteList
);

export const getImagingChartData = createSelector(
  getImagingState,
  state => state.imagingChartData
);

export const getTemplateFormData = createSelector(
  getImagingState,
  state => state.selectedTemplateFormData
);
