import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { Store, select } from '@ngrx/store';
import { ImagingState } from '@app/modules/imaging/models';
import { Observable } from 'rxjs';
import { getSelectedTestOrder } from '@app/modules/imaging/reducers';
import { map, tap } from 'rxjs/operators';

@Injectable()
export class SelectedTestGuard implements CanActivate {
  private selectedTestOrder$: Observable<any>;

  constructor(private router: Router, private store: Store<ImagingState>) {
    this.selectedTestOrder$ = store.pipe(select(getSelectedTestOrder));
  }

  canActivate() {
    return this.selectedTestOrder$.pipe(
      map(selectedTest => !!selectedTest.testId),
      tap(testExist => {
        if (!testExist) {
          this.router.navigate(['imaging', 'test-order']);
        }
      })
    );
  }
}
