import {
  Directive,
  Input,
  ViewContainerRef,
  OnInit,
  ComponentFactoryResolver,
  ComponentRef
} from '@angular/core';
import { FormGroup, NgForm } from '@angular/forms';
import { DynamicFormFieldConfig, FormField } from '@app/modules/imaging/models';
import { FormInputControlComponent } from '@app/modules/imaging/components/dynamic-form/form-components/input/input.component';
import { FormTextAreaControlComponent } from '@app/modules/imaging/components/dynamic-form/form-components/textarea/textarea.component';

const components = new Map()
  .set('ALPHANUMERIC', FormInputControlComponent)
  .set('TEXT', FormInputControlComponent)
  .set('TEXTAREA', FormTextAreaControlComponent);

@Directive({
  selector: '[appDynamicField]'
})
export class DynamicFieldDirective implements OnInit {
  @Input() config: DynamicFormFieldConfig;
  @Input() group: FormGroup;
  @Input() parentNgForm: NgForm;

  private component: ComponentRef<FormField>;

  constructor(
    private componentFactoryResolver: ComponentFactoryResolver,
    private container: ViewContainerRef
  ) {}

  ngOnInit() {
    // validation field will contian the form control type
    if (components.has(this.config.validation)) {
      const component = components.get(this.config.validation);
      const factory = this.componentFactoryResolver.resolveComponentFactory<
        FormField
      >(component);
      this.component = this.container.createComponent(factory);
      this.component.instance.config = this.config;
      this.component.instance.group = this.group;
      this.component.instance.formGroupDir = this.parentNgForm;
    }
  }
}
