import { Injectable } from '@angular/core';
import { Actions, Effect, ofType } from '@ngrx/effects';
import { Action } from '@ngrx/store';
import {
  ImagingActionTypes,
  GetTestOrderData,
  GetTestOrderDataSuccess,
  UpdateTestOrderData,
  ImagingApiError,
  GetEmployee,
  GetEmployeeSuccess,
  SaveImagePaths,
  UpdateTestOrderDataSuccess,
  SaveImagePathsError,
  GetTemplateColumns,
  GetTemplateColumnSuccess,
  GetTestImagePaths,
  GetTestImagePathsSuccess,
  GetTestImagePathsFailure,
  GetTemplateColumnFailure,
  SaveResultEntryFormData,
  SaveResultEntryFormDataSuccess,
  GetGivenBy,
  GetGivenBySuccess,
  GetDrugRoutes,
  GetDrugRouteSuccess,
  GetImagingChartData,
  GetImagingChartDataError,
  GetImagingChartDataSuccess,
  GetTemplateColumnsData,
  GetTemplateColumnsDataSuccess,
  GetTemplateColumnsDataError
} from '../actions';

import { Observable, of, forkJoin, throwError } from 'rxjs';

import {
  map,
  catchError,
  switchMap,
  flatMap,
  concatMap,
  filter
} from 'rxjs/operators';
import { ToastrService } from 'ngx-toastr';
import { ImagingService } from '@app/modules/imaging/services/imaging.service';
import { HttpErrorResponse } from '@angular/common/http';
import { UtilService } from '@core/services/utils.service';
import {
  ImagingApiService,
  LabApiService,
  OpdApiService
} from '@core/services';
import {
  Employee,
  TestOrder,
  ParsedAge,
  ImagePaths
} from '@app/modules/imaging/models';
import { DrugRoute } from '@app/modules/imaging/models/drug-route';
import { ImagingChartData } from '@app/modules/imaging/models/imaging-chart-data';
import { ApiResponse } from '@core/models/api-response';

@Injectable()
export class ImagingEffects {
  constructor(
    private utilService: UtilService,
    private imagingApiService: ImagingApiService,
    private labApiService: LabApiService,
    private opdApiService: OpdApiService,
    private action$: Actions,
    private imagingService: ImagingService,
    private toastr: ToastrService
  ) {}

  /**
   * fetches employees data
   */
  @Effect()
  getEmployee$: Observable<Action> = this.action$.pipe(
    ofType<GetEmployee>(ImagingActionTypes.GetEmployee),
    switchMap(action => {
      const actionPayload = action.payload;
      const employeeDesignationsObservables = actionPayload.designations.map(
        (designation: string) => {
          const apiPayload = {
            designation,
            orgId: actionPayload.orgId,
            orgGroupId: actionPayload.orgGroupId,
            authenticatedUserId: actionPayload.regId,
            orgGrpId: actionPayload.orgGroupId,
          };
          return this.imagingApiService
            .getEmpListBasedOnDesignation<Array<Employee>>(apiPayload)
            .pipe(
              map(response => {
                if (response.responseCode === 'E200') {
                  return response.data.map(val => {
                    val.designation = designation;
                    return val;
                  });
                } else {
                  return throwError(response.responseMessage);
                }
              })
            );
        }
      );
      return forkJoin(employeeDesignationsObservables).pipe(
        map((data: any[]) => {
          return data.reduce((acc, val) => (acc = [...acc, ...val]));
        }),
        map((data: any[]) => {
          return new GetEmployeeSuccess(data);
        }),
        catchError(err => {
          return of(new ImagingApiError(err));
        })
      );
    })
  );

  /**
   * fetches dynamic templates
   */
  @Effect()
  getTemplateColumns$: Observable<Action> = this.action$.pipe(
    ofType<GetTemplateColumns>(ImagingActionTypes.GetTemplateColumns),
    switchMap(action => {
      return this.imagingApiService
        .fetchTemplateIdForGivenTest<number>(action.payload)
        .pipe(
          map(response => {
            const { data } = response;
            if (response.responseCode === 'E200') {
              return data;
            } else if (response.responseCode === 'E400') {
              return throwError(response);
            }
          }),
          flatMap(templateId => {
            const payload = {
              templateId,
              ...action.payload
            };
            return this.imagingApiService.fetchTemplateColumns(payload).pipe(
              map(response => {
                const { data } = response;
                if (response.responseCode === 'E200') {
                  return new GetTemplateColumnSuccess(data);
                } else if (response.responseCode === 'E400') {
                  return new GetTemplateColumnFailure();
                }
              })
            );
          }),
          catchError(() => {
            return of(new GetTemplateColumnFailure());
          })
        );
    })
  );

  /**
   * fetches dynamic template data
   */
  @Effect()
  getTemplateColumnsData$: Observable<Action> = this.action$.pipe(
    ofType<GetTemplateColumnsData>(ImagingActionTypes.GetTemplateColumnsData),
    switchMap(action => {
      return this.labApiService.fetchDynamicTemplateData(action.payload).pipe(
        map(response => {
          const { data } = response;
          if (response.responseCode === 'E200') {
            return new GetTemplateColumnsDataSuccess(data);
          } else if (response.responseCode === 'E400') {
            return new GetTemplateColumnsDataError(response);
          }
        }),
        catchError(err => {
          return of(new GetTemplateColumnsDataError(err));
        })
      );
    })
  );

  /**
   * saves dymaic template data
   */
  @Effect()
  saveResultEntryFormData$: Observable<Action> = this.action$.pipe(
    ofType<SaveResultEntryFormData>(ImagingActionTypes.SaveResultEntryFormData),
    switchMap(action => {
      const operator = <T>(source: Observable<ApiResponse<T>>) =>
        source.pipe(
          map(response => {
            const { data } = response;
            if (response.responseCode === 'E200') {
              this.toastr.success(response.responseMessage, 'Success!');
              return new SaveResultEntryFormDataSuccess(data);
            } else if (response.responseCode === 'E400') {
              return new ImagingApiError(response);
            }
          }),
          catchError(error => {
            return of(new ImagingApiError(error));
          })
        );

      return this.labApiService[
        action.payload.data
          ? 'insertOrUpdateDataForTemplate'
          : 'updateImageTestResult'
      ](action.payload).pipe(operator);
    })
  );

  /**
   * fetches test order data
   */
  @Effect()
  getTestOrderData$: Observable<Action> = this.action$.pipe(
    ofType<GetTestOrderData>(ImagingActionTypes.GetTestOrderData),
    switchMap(action => {
      return this.imagingApiService
        .fetchOrderEntryBasedOnSearchFilter<Array<TestOrder>>(action.payload)
        .pipe(
          map(response => {
            let { data } = response;
            if (response.responseCode === 'E200') {
              data = data.map(test => {
                if (test.age) {
                  const ageMap = this.utilService.parseAgeIndays(test.age);
                  test.parsedAge = <ParsedAge>(
                    this.utilService.mapToObject(ageMap)
                  );
                } else if (test.birthDt) {
                  const ageMap = this.utilService.parseDob(test.birthDt);
                  test.parsedAge = <ParsedAge>(
                    this.utilService.mapToObject(ageMap)
                  );
                }
                return test;
              });
              return new GetTestOrderDataSuccess(data);
            } else if (response.responseCode === 'E400') {
              return new ImagingApiError(response);
            }
          }),
          catchError(error => {
            return of(new ImagingApiError(error));
          })
        );
    })
  );

  /**
   * fetches test image paths
   */
  @Effect()
  getTestImagePaths$: Observable<Action> = this.action$.pipe(
    ofType<GetTestImagePaths>(ImagingActionTypes.GetTestImagePaths),
    switchMap(action => {
      return this.imagingApiService
        .fetchRadioLogyTestImagePaths<Array<ImagePaths>>(action.payload)
        .pipe(
          map(response => {
            const { data } = response;
            if (response.responseCode === 'E200') {
              return new GetTestImagePathsSuccess(data);
            } else if (response.responseCode === 'E400') {
              return new GetTestImagePathsFailure();
            }
          }),
          catchError(() => {
            return of(new GetTestImagePathsFailure());
          })
        );
    })
  );

  @Effect()
  getImagingChartData$: Observable<Action> = this.action$.pipe(
    ofType<GetImagingChartData>(ImagingActionTypes.GetImagingChartData),
    switchMap(action => {
      return this.imagingApiService
        .fetchImagingChartResponse<Array<ImagingChartData>>(action.payload)
        .pipe(
          map(response => {
            const { data } = response;
            if (response.responseCode === 'E200') {
              return new GetImagingChartDataSuccess(data);
            } else if (response.responseCode === 'E400') {
              return new GetImagingChartDataError(response);
            }
          }),
          catchError(err => {
            return of(new GetImagingChartDataError(err));
          })
        );
    })
  );

  /**
   * get given by list
   */
  @Effect()
  getGivenBy$: Observable<Action> = this.action$.pipe(
    ofType<GetGivenBy>(ImagingActionTypes.GetGivenBy),
    switchMap(action => {
      return this.imagingApiService.fetchGivenByList(action.payload).pipe(
        map((response: any) => {
          const { data } = response;
          if (response.responseCode === 'E200') {
            return new GetGivenBySuccess(data);
          } else if (response.responseCode === 'E400') {
            return new ImagingApiError(response);
          }
        }),
        catchError(error => {
          return of(new ImagingApiError(error));
        })
      );
    })
  );

  /**
   * get drug route list
   */
  @Effect()
  getDrugRouteList$: Observable<Action> = this.action$.pipe(
    ofType<GetDrugRoutes>(ImagingActionTypes.GetDrugRoutes),
    switchMap(action => {
      return this.imagingApiService
        .fetchDrugRouteTypes<Array<DrugRoute>>(action.payload)
        .pipe(
          map((response: any) => {
            const { data } = response;
            if (response.responseCode === 'E200') {
              return new GetDrugRouteSuccess(data);
            } else if (response.responseCode === 'E400') {
              return new ImagingApiError(response);
            }
          }),
          catchError(error => {
            return of(new ImagingApiError(error));
          })
        );
    })
  );

  /**
   * saves image paths
   */
  @Effect()
  SaveImagePaths$: Observable<Action> = this.action$.pipe(
    ofType<SaveImagePaths>(ImagingActionTypes.SaveImagePaths),
    switchMap(action => {
      const actionPayload = action.payload;
      const {
        analysisRequestTestId,
        createdUsrId,
        orgGrpId,
        orgId
      } = actionPayload;
      const payload = actionPayload.dicomImagePaths.map(image => {
        return {
          dicomImagePath: image.name,
          isDicom: image.isDicom,
          analysisRequestTestId,
          createdUsrId,
          orgGrpId,
          orgId
        };
      });
      return this.imagingApiService.saveUploadedImagePaths(payload).pipe(
        map(response => {
          if (response.responseCode === 'E200') {
            return new GetTestImagePaths({
              analysisRequestTestId,
              createdUsrId,
              orgGrpId,
              orgId
            });
          } else if (response.responseCode === 'E400') {
            return new SaveImagePathsError(action.payload);
          }
        }),
        catchError(() => {
          return of(new SaveImagePathsError(action.payload));
        })
      );
    })
  );

  /**
   * handles image path saving errors
   */
  @Effect({ dispatch: false })
  SaveImagePathsError$: Observable<Action> = this.action$.pipe(
    ofType<SaveImagePathsError>(ImagingActionTypes.SaveImagePathsError),
    concatMap(action => {
      this.toastr.error('Error occurred while saving files', 'Upload file');
      this.utilService
        .arrayChunker(
          action.payload.dicomImagePaths,
          this.imagingService.deleteObjectChunkSize
        )
        .forEach(chunk => {
          this.imagingService.deleteFromBucket(
            this.imagingService.createS3Configuration(),
            this.imagingService.imagingBucketName,
            chunk
          );
        });
      return of(null);
    }),
    catchError(_ => {
      this.toastr.error(
        'Error occurred while deleting unsaved files',
        'Upload file'
      );
      return of(null);
    })
  );

  /**
   * updates test order data
   */
  @Effect()
  updateTestOrderData$: Observable<Action> = this.action$.pipe(
    ofType<UpdateTestOrderData>(ImagingActionTypes.UpdateTestOrderData),
    concatMap(action => {
      return this.imagingApiService.updateOrderTestData(action.payload).pipe(
        map(response => {
          if (response.responseCode === 'E200') {
            return new UpdateTestOrderDataSuccess(action.payload);
          } else if (response.responseCode === 'E400') {
            return new ImagingApiError(response);
          }
        }),
        catchError(error => {
          return of(new ImagingApiError(error));
        })
      );
    })
  );

  /**
   * general api handler
   */
  @Effect({ dispatch: false })
  ImagingApiError$: Observable<Action> = this.action$.pipe(
    ofType<ImagingApiError>(ImagingActionTypes.ApiError),
    concatMap(action => {
      const { payload } = action;
      if (action.payload instanceof HttpErrorResponse) {
        this.toastr.error('Error occurred');
      } else if (payload.responseCode === 'E400') {
        this.toastr.error(payload.responseMessage);
      } else {
        this.toastr.error('Error occurred');
      }
      return of(null);
    })
  );
}
