import {
  Component,
  Inject,
  ViewChild,
  ElementRef,
  AfterViewInit,
  OnDestroy,
  OnInit,
  Renderer2
} from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material';
import {
  TOOLS_BUTTON_PROVIDER,
  TOOLS_TOKEN
} from '@app/modules/imaging/services/diacom-tools-buttons.service';
import { CORNERSTONE, CORNERSTONETOOLS, UtilService } from '@core/services';
import { fromEvent, Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';

@AutoUnsubscribe()
@Component({
  templateUrl: './diacom-viewer.component.html',
  styleUrls: ['./diacom-viewer.component.scss'],
  providers: [TOOLS_BUTTON_PROVIDER]
})
export class DiacomViewerComponent implements AfterViewInit, OnDestroy, OnInit {
  @ViewChild('viewerMount') viewerMount: ElementRef;
  @ViewChild('viewerCanvas') viewerCanvas: ElementRef;

  imageUrls: Array<string>;
  newImageIndex = 0;
  frameRate = 0;
  isPlaying = false;
  wcWw: string | number;
  zoom: string;
  activatedTool: string;
  firstImageLoaded = false;

  readonly FRAME_RATE = 25;
  readonly DEBOUNCE_TIME = 400;
  INITIAL_WW;
  INITIAL_WC;

  private windowResizeSubscription: Subscription;
  private firstImageUrl: string;
  private imageStack;

  constructor(
    private renderer: Renderer2,
    @Inject(TOOLS_TOKEN) public tools,
    @Inject(MAT_DIALOG_DATA) data: any,
    @Inject(CORNERSTONE) private cornerstone: any,
    @Inject(CORNERSTONETOOLS) private cornerstoneTools: any,
    private utilService: UtilService
  ) {
    this.imageUrls = data.map(url => `wadouri:${url}`);
    [this.firstImageUrl] = this.imageUrls;
    this.imageStack = {
      currentImageIdIndex: 0,
      imageIds: this.imageUrls
    };
  }

  /**
   * activates a tool
   * @param {Object} tool a tool to activate
   */
  activateTool(tool) {
    this.activatedTool = tool.toolName;
    this.deactivateAllTools();
    const toolFn = this.cornerstoneTools[tool.toolName];
    if (toolFn) {
      toolFn.activate(this.viewerMount.nativeElement, tool.mouseButtonMask);
    }
  }

  /**
   * attaches a window resize listener so that the canvas can be resized whenever window resizes
   */
  attachWindowResizeListener() {
    this.windowResizeSubscription = fromEvent(window, 'resize')
      .pipe(debounceTime(this.DEBOUNCE_TIME))
      .subscribe(() => {
        const {
          width,
          height
        } = this.viewerMount.nativeElement.getBoundingClientRect();
        this.viewerCanvas.nativeElement.style.width = `${width}px`;
        this.viewerCanvas.nativeElement.style.height = `${height}px`;
        this.cornerstone.resize(this.viewerMount.nativeElement);
      });
  }

  attachOnNewImageListener() {
    this.renderer.listen(
      this.viewerMount.nativeElement,
      this.cornerstone.EVENTS.NEW_IMAGE,
      evt => {
        const eventData = evt.detail;
        this.newImageIndex = this.imageStack.currentImageIdIndex;
        this.frameRate = eventData.frameRate;
      }
    );
  }

  attachImageRenderListener() {
    const once = this.utilService.once(function(this: DiacomViewerComponent) {
      const viewport = this.getViewPort();
      this.INITIAL_WC = viewport.voi.windowCenter;
      this.INITIAL_WW = viewport.voi.windowWidth;
    }, this);
    this.renderer.listen(
      this.viewerMount.nativeElement,
      this.cornerstone.EVENTS.IMAGE_RENDERED,
      () => {
        const viewport = this.getViewPort();
        this.wcWw = viewport
          ? `WW ${Math.round(viewport.voi.windowWidth)} / WC ${Math.round(
              viewport.voi.windowCenter
            )}`
          : 0;
        once();
        this.zoom = viewport ? `${viewport.scale.toFixed(2)}X` : '0.00X';
      }
    );
  }

  configureStackStateManager() {
    this.cornerstoneTools.addStackStateManager(this.viewerMount.nativeElement, [
      'stack',
      'playClip'
    ]);
    this.cornerstoneTools.addToolState(
      this.viewerMount.nativeElement,
      'stack',
      this.imageStack
    );
  }

  deactivateAllTools() {
    this.tools.forEach(value => {
      this.cornerstoneTools[value.toolName].deactivate(
        this.viewerMount.nativeElement,
        value.mouseButtonMask
      );
    });
  }

  enableScrollIndicator() {
    this.cornerstoneTools.scrollIndicator.enable(
      this.viewerMount.nativeElement
    );
  }

  enableStackScroll() {
    this.cornerstoneTools.stackScrollWheel.activate(
      this.viewerMount.nativeElement
    );
  }

  enableMouseWheelInput() {
    this.cornerstoneTools.mouseInput.enable(this.viewerMount.nativeElement);
    this.cornerstoneTools.mouseWheelInput.enable(
      this.viewerMount.nativeElement
    );
  }

  getViewPort() {
    return this.cornerstone.getViewport(
      this.viewerMount.nativeElement
    );
  }

  loadFirstImageAndDisplay() {
    this.cornerstone.loadAndCacheImage(this.firstImageUrl).then(image => {
      const viewport = this.getViewPort();
      this.cornerstone.displayImage(
        this.viewerMount.nativeElement,
        image,
        viewport
      );
      this.configureStackStateManager();
      this.enableMouseWheelInput();
      this.enableScrollIndicator();
      this.enableStackScroll();
      this.firstImageLoaded = true;
    });
  }

  ngAfterViewInit() {
    this.cornerstone.enable(this.viewerMount.nativeElement);
  }

  ngOnDestroy() {
    this.stop();
  }

  ngOnInit() {
    this.attachWindowResizeListener();
    this.attachImageRenderListener();
    this.attachOnNewImageListener();
    this.loadFirstImageAndDisplay();
  }

  play() {
    this.cornerstoneTools.playClip(
      this.viewerMount.nativeElement,
      this.FRAME_RATE
    );
    this.isPlaying = true;
  }

  reset() {
    this.cornerstoneTools.appState.restore({
      imageIdToolState: {},
      elementToolState: {},
      elementViewport: {}
    });
    this.cornerstone.updateImage(this.viewerMount.nativeElement);
    const viewport = this.getViewPort();
    viewport.voi.windowWidth = this.INITIAL_WW;
    viewport.voi.windowCenter = this.INITIAL_WC;
    viewport.translation = {
      x: 0,
      y: 0
    };
    viewport.rotation = 0;
    this.cornerstone.setViewport(this.viewerMount.nativeElement, viewport);
  }

  stop() {
    this.cornerstoneTools.stopClip(this.viewerMount.nativeElement);
    this.isPlaying = false;
  }
}
