import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FlexLayoutModule } from '@angular/flex-layout';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { TranslateModule } from '@ngx-translate/core';
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { MatProgressSpinnerModule } from '@angular/material/progress-spinner';
// import { LazyLoadImageModule } from 'ng-lazyload-image';
import { MatTooltipModule } from '@angular/material/tooltip';

import { reducers } from './reducers';
import { ImagingEffects } from './effects';
import { SharedModule } from '@shared/shared.module';
import { ValidUserGuard } from '@app/modules/imaging/services/valid-user-guard.service';
import { ROLES_TOKEN, ROLES } from './services/roles-constants.service';
import { ResultEntryComponent } from './containers/result-entry/result-entry.component';
import { ProfileCardComponent } from './components/profile-card/profile-card.component';
import { ResultWrapperComponent } from './containers/result-entry-wrapper/result-entry-wrapper.component';
import {
  TEST_STATUS_LIST,
  testStatusList
} from './services/test-status-list-constants.service';
import { FileSelectorDirective } from './directives/file-selector.directive';
import { DynamicFormComponent } from './components/dynamic-form/dynamic-form.component';
import { FormInputControlComponent } from '@app/modules/imaging/components/dynamic-form/form-components/input/input.component';
import { DynamicFieldDirective } from '@app/modules/imaging/directives/dynamic-field.directive';
import { FormTextAreaControlComponent } from '@app/modules/imaging/components/dynamic-form/form-components/textarea/textarea.component';
import { PastOrdersComponent } from './containers/past-orders/past-orders.component';
import { ImagingRoutingModule } from './imaging-routing.module';
import { TestOrdersComponent } from './containers/test-orders/test-orders.component';
import { ImagingService } from './services/imaging.service';
import { SelectedTestGuard } from './services/selected-test-guard.service';
import { OwlDateTimeModule, OwlNativeDateTimeModule } from 'ng-pick-datetime';
import { CheckBoxComponent } from '@app/modules/imaging/components/checkbox/checkbox.component';
import { MatDialogModule } from '@angular/material';
import { DiacomViewerComponent } from './containers/diacom-viewer/diacom-viewer.component';
import { HighchartsChartModule } from 'highcharts-angular';
import { HttpClientModule } from '@angular/common/http';
import { AngularEditorModule } from '@kolkov/angular-editor';
@NgModule({
  imports: [
    HighchartsChartModule,
    MatTooltipModule,
    MatProgressSpinnerModule,
    // LazyLoadImageModule,
    FlexLayoutModule,
    TranslateModule,
    CommonModule,
    FormsModule,
    MatDatepickerModule,
    MatButtonToggleModule,
    ReactiveFormsModule,
    SharedModule,
    MatDialogModule,
    StoreModule.forFeature('IMAGING_FEATURE', reducers),
    EffectsModule.forFeature([ImagingEffects]),
    ImagingRoutingModule,
    OwlDateTimeModule,
    OwlNativeDateTimeModule,
    HttpClientModule,
    AngularEditorModule
  ],
  declarations: [
    FileSelectorDirective,
    TestOrdersComponent,
    CheckBoxComponent,
    DiacomViewerComponent,
    ResultEntryComponent,
    ProfileCardComponent,
    ResultWrapperComponent,
    DynamicFormComponent,
    FormInputControlComponent,
    DynamicFieldDirective,
    FormTextAreaControlComponent,
    PastOrdersComponent
  ],
  entryComponents: [FormInputControlComponent, FormTextAreaControlComponent, DiacomViewerComponent],
  providers: [
    ValidUserGuard,
    ImagingService,
    SelectedTestGuard,
    { provide: TEST_STATUS_LIST, useValue: testStatusList },
    { provide: ROLES_TOKEN, useValue: ROLES }
  ]
})
export class ImagingModule {}
