import {
  ImagingActions,
  ImagingActionTypes
} from '@app/modules/imaging/actions';
import { ImagePaths } from '@app/modules/imaging/models';
export interface State {
  isLoading: boolean;
  imagePaths: Array<ImagePaths>;
  errorOccurred: boolean;
}

const initialState: State = {
  isLoading: false,
  imagePaths: [],
  errorOccurred: false
};

export function reducer(state = initialState, action: ImagingActions) {
  switch (action.type) {
    case ImagingActionTypes.GetTestImagePaths: {
      return {
        isLoading: true,
        errorOccurred: false,
        imagePaths: []
      };
    }
    case ImagingActionTypes.GetTestImagePathsSuccess: {
      return {
        isLoading: false,
        errorOccurred: false,
        imagePaths: [...action.payload]
      };
    }
    case ImagingActionTypes.GetTestImagePathsFailure: {
      return {
        isLoading: false,
        errorOccurred: true,
        imagePaths: []
      };
    }
    default:
      return state;
  }
}
