import {
  ImagingActions,
  ImagingActionTypes
} from '@app/modules/imaging/actions';

const initialState = [];

export function reducer(state = initialState, action: ImagingActions) {
  switch (action.type) {
    case ImagingActionTypes.GetGivenBySuccess: {
      return [...action.payload];
    }
    default:
      return state;
  }
}
