import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { TestOrdersComponent } from './containers/test-orders/test-orders.component';
import { ResultWrapperComponent } from '@app/modules/imaging/containers/result-entry-wrapper/result-entry-wrapper.component';

export const ImagingRoutes: Routes = [
  {
    path: 'test-order',
    component: TestOrdersComponent,
    data: {
      title: 'Test Orders'
    }
  },
  {
    path: 'result-entry',
    component: ResultWrapperComponent,
    data: {
      title: 'Result Entry'
    }
  },
  { path: '', redirectTo: 'test-order', pathMatch: 'prefix' }
];

@NgModule({
  imports: [RouterModule.forChild(ImagingRoutes)],
  exports: [RouterModule]
})
export class ImagingRoutingModule {}
