import { InjectionToken } from '@angular/core';

export enum TestStatusEnum {
  DUE = 'sample due',
  TEST_DONE = 'Test Done',
  FOR_REVIEW = 'For Review',
  REVIEWED = 'Reviewed',
  PUBLISHED = 'Published'
}

export const TEST_STATUS_LIST = new InjectionToken<Array<string>>(
  'TEST_STATUS_LIST_TOKEN'
);
export const testStatusList = [
  TestStatusEnum.DUE,
  TestStatusEnum.FOR_REVIEW,
  TestStatusEnum.REVIEWED,
  TestStatusEnum.PUBLISHED,
  TestStatusEnum.TEST_DONE
];
