import {
  Component,
  Inject,
  OnInit,
  OnDestroy,
  ChangeDetectorRef
} from '@angular/core';
import { Router } from '@angular/router';
import { first } from 'rxjs/operators';
import { ImagingService } from '../../services/imaging.service';
import { Subscription, Observable } from 'rxjs';
import { DatePipe } from '@angular/common';
import { MatDatepicker } from '@angular/material/datepicker';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';

import {
  Employee,
  FilterFormData,
  TestOrder,
  OrderEntryRes,
  ImagingState
} from '../../models';
import { Store, select } from '@ngrx/store';

import {
  GetTestOrderData,
  UpdateTestOrderData,
  GetEmployee,
  SaveCurrentSelectedTestOrder,
  SaveFilterState,
  GetImagingChartData
} from '../../actions';
import {
  getDoctors,
  getTechnicians,
  getTestOrders,
  getFilterFormState,
  getImagingChartData
} from '@app/modules/imaging/reducers';
import { ROLES_TOKEN } from '@app/modules/imaging/services/roles-constants.service';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import {
  TEST_STATUS_LIST,
  TestStatusEnum
} from '@app/modules/imaging/services/test-status-list-constants.service';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import {
  ChartSlice
} from '@app/modules/imaging/models/imaging-chart-data';
import * as fromChartDataReducer from '@app/modules/imaging/reducers/imaging-chart-data.reducer';
import { HIGHCHARTS } from '@core/services';
import { slideDownAnimation } from '@core/animations';
import { IPatientData } from '../../models/patient-data';
import { ValidUserGuard } from '../../services/valid-user-guard.service';
import { ReportsApiServce } from '@core/services';
import { ToastrService } from 'ngx-toastr';
@AutoUnsubscribe()
@Component({
  templateUrl: './test-orders.component.html',
  styleUrls: ['./test-orders.component.scss'],
  animations: [slideDownAnimation]
})
export class TestOrdersComponent implements OnInit, OnDestroy {

  chartInstances: Array<object> = [];
  chartsCollapsed = false;
  chartsDataLoading = false;
  chartOptions;
  chartsList: Array<any>;
  doctorListSubscription: Subscription;
  doctorList: Employee[];
  doctorList$: Observable<Employee[]>;
  filterForm$: Observable<FilterFormData>;
  filterFormDataSubscription: Subscription;
  filtersVisible = false;
  filterForm: FormGroup;
  imagingChartData$: Observable<fromChartDataReducer.State>;
  imagingChartDataSubscription: Subscription;
  searchPatiendId: string;
  searchOrderId: string;
  technicianList$: Observable<Employee[]>;
  technicianListSubscription: Subscription;
  technicianList: Employee[];
  testOrders$: Observable<TestOrder[]>;
  testOrders: Array<TestOrder>;
  testOrdersSubscription: Subscription;

  private readonly roleMap: Map<string, number> = this.roles;
  private sessionData: UserSession;
  patientData: any;

  constructor(
    @Inject(ROLES_TOKEN) private roles: Map<string, number>,
    @Inject(TEST_STATUS_LIST) public testStatusList: Array<string>,
    @Inject(HIGHCHARTS) public highcharts,
    private imagingService: ImagingService,
    private datePipe: DatePipe,
    private store: Store<ImagingState>,
    private formBuilder: FormBuilder,
    private router: Router,
    private userService: UserService,
    private validUserGuard: ValidUserGuard,
    private reportsApiServce : ReportsApiServce,
    private toaster: ToastrService,
  ) {
    this.sessionData = this.userService.getUser();
    this.doctorList$ = store.pipe(select(getDoctors));
    this.technicianList$ = store.pipe(select(getTechnicians));
    this.testOrders$ = store.pipe(select(getTestOrders));
    this.imagingChartData$ = store.pipe(select(getImagingChartData));
    this.filterForm$ = store.pipe(select(getFilterFormState));
    this.chartOptions = {
      testDue: this.imagingService.chartObjectFactory('Test Due'),
      testDone: this.imagingService.chartObjectFactory('Test Done'),
      forReview: this.imagingService.chartObjectFactory('Verification Done'),
      published: this.imagingService.chartObjectFactory('Published')
    };
  }

  /**
   * @param requiredRoles An array of roles a user needs to have to access an item
   * @param userRoles An array of roles which a user has
   * @returns {boolean} A boolean value to let know if an user is allowed to access a resource
   */
  allowToView(requiredRoles: Array<string>, userRole: string): boolean {
    if (requiredRoles.indexOf(userRole) !== -1) {
      return true;
    } else {
      return false;
    }
  }

  /**
   * This gets executed whenever a filtering is performed
   */
  applyFilter() {
    this.store.dispatch(new GetTestOrderData(this.getSearchPayload()));
    this.store.dispatch(
      new SaveFilterState(this.filterForm.getRawValue() as FilterFormData)
    );
  }

  checkIfUserIsValid() {
    const isValidUser = this.validUserGuard.isValidUser();
    if (!isValidUser) {
      this.router.navigate(['no-access']);
    }
  }

  /**
   * Helper function to update the contrast of the given row
   * @param state The previous sate of the contrast btn
   * @param item The row on which the contrast state to be updated
   */
  changeContrast(btnState: boolean, item: any) {
    item.contrastRqrd = btnState;
    this.updateData(item);
  }

  /**
   * Toggles datepicker
   * @param datePicker A reference to the date picker
   */
  datePickerToggler(datePicker: MatDatepicker<Date>) {
    datePicker[datePicker.opened ? 'close' : 'open']();
  }

  /**
   * This method dispatches an action to the store
   * to fetch employees based on their designations
   * @param designations An array of user's designations
   */
  fetchEmpList(designations: string[]) {
    const payload = {
      designations,
      orgId: this.sessionData.orgId,
      orgGroupId: this.sessionData.orgGroupId,
      authenticatedUserId: this.sessionData.regId
    };
    this.store.dispatch(new GetEmployee(payload));
  }

  /**
   * This method dispatches an action to the store
   * to fetch current day's imaging data to be ploted on charts
   */
  fetchImagingChartData() {
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgGrpId: this.sessionData.orgGroupId,
      orgId: this.sessionData.orgId
    };
    this.store.dispatch(new GetImagingChartData(payload));
  }

  /**
   * This method navigates to result entry screen
   * This method saves current selected test order
   * @param {OrderEntryRes} item
   * @param {TestOrder} order
   */
  goToResultEntryScreen(item: OrderEntryRes, order: TestOrder) {
    const copiedOrder = Object.assign({}, order);
    const patientData: IPatientData = {
      analysisRequestId: copiedOrder.analysisRequestId,
      imagePath: copiedOrder.imagePath,
      parsedAge: copiedOrder.parsedAge,
      patientId: copiedOrder.patientId,
      gender: copiedOrder.gender,
      mobileNo: copiedOrder.mobileNo,
      bloodGroup: copiedOrder.blodGrpNm,
      hospitalPatientId: copiedOrder.hosPatientId,
      patientName: copiedOrder.patientName,
      prescribedBy: copiedOrder.prescribedBy,
      orderDate: copiedOrder.orderDate,
      patientVisitId: copiedOrder.patientVisitId
    };
    this.store.dispatch(
      new SaveCurrentSelectedTestOrder({ ...item, ...patientData })
    );
    this.router.navigate(['imaging', 'result-entry']);
  }

  /**
   * A helper function that returns current filter parameters
   */
  getSearchPayload() {
    const formValue = this.filterForm.getRawValue() as FilterFormData;
    const payload = {
      createdUsrId: this.sessionData.regId,
      orgId: this.sessionData.orgId,
      fromDate: this.datePipe.transform(formValue.fromDate, 'yyyy-MM-dd'),
      toDate: this.datePipe.transform(formValue.toDate, 'yyyy-MM-dd'),
      imagingVerifyDocorId: formValue.doctor,
      testStatus: formValue.testStatus,
      imagingTechnicianId: formValue.technician
    };
    return payload;
  }

  ngOnDestroy() {}

  /**
   * Initializes the filter form using [initFilterForm]{@link TestOrdersComponent#initFilterForm}.
   * Initializes various observables subscription using [initSubscription]{@link TestOrdersComponent#initSubscription}
   * Applies the filter for the first time so to get today's test order using applyFilter.
   * Fetches employees list using [fetchEmpList]{@link TestOrdersComponent#fetchEmpList}
   * Fetches current day's chart data using [fetchImagingChartData]{@link TestOrdersComponent#fetchImagingChartData}
   */
  ngOnInit() {

    this.checkIfUserIsValid();

    this.initFilterForm();
    this.initSubscriptions();
    this.applyFilter();
    this.fetchEmpList(['Doctor', 'Technician']);
    if (this.userHighestRole === 'Doctor') {
      this.fetchImagingChartData();
    }
    this.chartsList = [{
      option: this.chartOptions.testDue,
      name: 'IMAGING.TEST_DUE'
    }, {
      option: this.chartOptions.testDone,
      name: 'IMAGING.TEST_DONE'
    }, {
      option: this.chartOptions.forReview,
      name: 'IMAGING.VERIFICATION_DONE'
    }, {
      option: this.chartOptions.published,
      name: 'IMAGING.PUBLISH_REPORT'
    }];

  }

  /**
   * Saves the charts instance in [chartInstance]{@link TestOrdersComponent#chartInstances} array
   * @param {Highcharts.ChartObject} chartInstance
   */
  saveChartInstances() {
    return (chartInstance: any) => {
      this.chartInstances.push(chartInstance);
    };
  }

  /**
   * This method dispatches an action to the store to fetche test orders based on order id
   * @param {number} orderId
   */
  searchPatientOnOrderId(orderId) {
    const payload = {
      analysisRequestId: orderId,
      ...this.getSearchPayload()
    };
    this.store.dispatch(new GetTestOrderData(payload));
  }

  /**
   * This method dispatches an action to the store to fetch test orders based on patient id
   * @param {string} patientId
   */
  searchPatientOnPatientId(patientId) {
    const payload = {
      hosPatientId: patientId,
      ...this.getSearchPayload()
    };
    this.store.dispatch(new GetTestOrderData(payload));
  }

  publishReport(item) {
    const payload = {
      ...item,
      publishedTime: new Date().getTime(),
      testStatus: TestStatusEnum.PUBLISHED
    };
    this.updateData(payload);
  }

  /**
   * Toggles the filter's visibility state.
   */
  toggleFilters() {
    this.filtersVisible = !this.filtersVisible;
  }

  /**
   *
   */
  updateData(item: any) {
    const payload = [
      {
        analysisRequestTestId: item.analysisRequestTestId,
        contrastRqrd: item.contrastRqrd,
        createdUsrId: this.sessionData.regId,
        imagingTechnicianId: item.imagingTechnicianId,
        imagingVerifiedDoctorId: item.imagingVerifyDoctorId,
        orgId: this.sessionData.orgId,
        publishedTime: item.publishedTime,
        testStatus: item.testStatus
      }
    ];
    this.store.dispatch(new UpdateTestOrderData(payload));
  }

  /**
   * Toggles the charts' visibility state
   */
  toggleCharts() {
    this.chartInstances = [];
    this.chartsCollapsed = !this.chartsCollapsed;
  }

  get userDesignations(): Array<string> {
    return this.sessionData.designationsList.map(
      desigObj => desigObj.designation
    );
  }

  get userHighestRole() {
    return this.imagingService.getUserHighestRole(
      this.roleMap,
      this.userDesignations
    );
  }

  /**
   * Creates the filter form
   */
  private initFilterForm() {
    const regId = this.sessionData.regId;
    this.filterForm = this.formBuilder.group({
      fromDate: [new Date(), Validators.required],
      toDate: [new Date(), Validators.required],
      doctor: null,
      technician: null,
      testStatus: null
    });
    switch (this.userHighestRole) {
      case 'Technician': {
        this.filterForm.patchValue({ technician: regId });
        break;
      }
      case 'Doctor': {
        this.filterForm.patchValue({ doctor: regId });
      }
    }
  }

  /**
   * @private
   * A helper method to initializes all the subscription in the component
   */
  private initSubscriptions() {
    this.technicianListSubscription = this.technicianList$.subscribe(
      technicians => (this.technicianList = technicians)
    );
    this.doctorListSubscription = this.doctorList$.subscribe(
      doctors => (this.doctorList = doctors)
    );
    this.testOrdersSubscription = this.testOrders$.subscribe(testOrders => {
      this.testOrders = testOrders;
      this.filtersVisible = !!!this.testOrders.length;
      // this.cdref.detectChanges();
    });
    this.filterFormDataSubscription = this.filterForm$
      .pipe(first())
      .subscribe((data: FilterFormData) => {
        this.filterForm.patchValue(data);
      });
    if (this.userHighestRole === 'Doctor') {
      this.imagingChartDataSubscription = this.imagingChartData$.subscribe(
        chartsData => {
          this.chartsDataLoading = chartsData.isLoading;
          const mapperFunc = (slice: ChartSlice) => ({
            name: slice.subGrouName,
            y: slice.testCount
          });
          Object.keys(chartsData.data).forEach(chartName => {
            if (this.chartOptions[chartName]) {
              this.chartOptions[chartName].series[0].data = chartsData.data[
                chartName
              ].map(mapperFunc);
            }
          });
        }
      );
    }
  }
  fetchImagingPrint(item) {
    const queryparams = {
      createdUserId: this.sessionData.regId,
      orgId: this.sessionData.orgId,
      analysisRequestTestId: item.analysisRequestTestId,
      verifyDoctorId: item.imagingVerifyDoctorId
    };
    this.reportsApiServce.fetchImagingPrint(queryparams).subscribe(
      response => {
        if (response instanceof ArrayBuffer) {
          const orderRePrint = new Blob([response], {
            type: 'application/pdf'
          });
          const reportURL = URL.createObjectURL(orderRePrint);
          const win = window.open('', '_blank');
          win.location.href = reportURL;
          setTimeout(() => {
            win.print();
          }, 1000);
        } else if (response.responseCode) {
          this.toaster.error('Failure!', response.responseMessage);
        }
      },
      () => {
        this.toaster.error('Failure!', 'Something went wrong');
      }
    );
  }
}
