import { Component } from '@angular/core';
import { FormGroup, NgForm } from '@angular/forms';
import { DynamicFormFieldConfig, FormField } from '@app/modules/imaging/models';

@Component({
  template: `
    <div class="p-t-5 p-b-5 p-l-5 p-r-5" [formGroup]="group">
      <label class="label-form">{{config.displayColumName}}</label>
      <input type="text"
        [formControlName]="config.columnName"
        class="bg-dark-white-smoke" />
        <div *ngIf="formGroupDir.submitted" class="has-error popover-style" role="alert">
          <div *ngIf="group.get(config.columnName).hasError('required')" class="dynamic_popover">* This field is required
            <span class="doArrow"></span>
          </div>
        </div>
    </div>
    `,
  styleUrls: ['../../../../_imaging.scss'],
  selector: 'app-dynamic-form-control'
})
export class FormInputControlComponent implements FormField {
  config: DynamicFormFieldConfig;
  group: FormGroup;
  formGroupDir: NgForm;
}
