import {
  ImagingActions,
  ImagingActionTypes
} from '@app/modules/imaging/actions';
import { TestOrder } from '@app/modules/imaging/models';

const initialState: any = {};

export function reducer(state = initialState, action: ImagingActions) {
  switch (action.type) {
    case ImagingActionTypes.SaveFilterState: {
      return Object.assign({}, action.payload);
    }
    default:
      return state;
  }
}
