import { Action } from '@ngrx/store';
import {
  Employee,
  TestOrder,
  FilterFormData,
  OrderEntryRes,
  IPatientData
} from '@app/modules/imaging/models';
import { ImagingChartData } from '@app/modules/imaging/models/imaging-chart-data';

export enum ImagingActionTypes {
  // employee
  GetEmployee                         = '[Imaging Test Orders Page] getEmployees',
  GetEmployeeSuccess                  = '[Imaging API] getEmployeeSuccess',

  // get test order data
  GetTestOrderData                    = '[Imaging Test Orders Page] getTestOrderData',
  GetTestOrderDataSuccess             = '[Imaging API] getTestOrderDataSuccess',

  // template
  GetTemplateColumns                  = '[Imaging Result Entry Page] fetchTemplateColumns',
  GetTemplateSuccess                  = '[Imaging API] fetchTemplateColumnSuccess',
  GetTemplateFailure                  = '[Imaging API] fetchTemplateColumnFailure',

  // get test images
  GetTestImagePaths                   = '[Imaging Result Entry Page] getTestImagePaths',
  GetTestImagePathsSuccess            = '[Imaging API] getTestImagePathsSuccess',
  GetTestImagePathsFailure            = '[Imaging API] getTestImagePathsFailure',

  // save test images
  SaveImagePaths                      = '[Imaging Result Entry Page] saveImagePaths',
  SaveImagPathsSuccess                = '[Imaging] saveImagePathsSuccess',
  SaveImagePathsError                 = '[Imaging] SaveImagePathsError',

  // update test order data
  UpdateTestOrderData                 = '[Imaging Test Orders Page] updateTestOrderData',
  UpdateTestOrderDataSuccess          = '[Imaging API] updateTestOrderDataSuccess',

  ApiError                            = '[Imaging API] imaging api error',

  SaveCurrentSelectedTestOrder        = '[Imaging Test Orders Page] save current selected test order',
  UpdateCurrentSelectedTestOrder      = '[Imaging Past Orders Page] update current selected test order',

  SaveFilterState                     = '[Imaging Test Orders Page] save filter state',

  // save result entry form data
  SaveResultEntryFormData             = '[Imaging Result Entry Page] save result entry form data',
  SaveResultEntryFormDataSuccess      = '[Imaging API] save result entry form data success',

  // given by in result entry screen
  GetGivenBy                          = '[Imaging Result Entry Page] get given by',
  GetGivenBySuccess                   = '[Imaging API] get given by success',

  // fetch drug routes actions
  GetDrugRoutes                       = '[Imaging Result Entry Page] get drug routes',
  GetDrugRouteSuccess                 = '[Imaging API] get drug routes success',

  GetImagingChartData                 = '[Imaging Test Order Page] get chart data',
  GetImagingChartDataSuccess          = '[Imaging API] get chart data success',
  GetImagingChartDataError            = '[Imaging API] get chart data error',

  GetTemplateColumnsData              = '[Imaging Result Entry Page] get template column data',
  GetTemplateColumnsDataSuccess       = '[Imaging API] get template column data success',
  GetTemplateColumnsDataError         = '[Imaging API] get template column data error'
}

// employees
export class GetEmployee implements Action {
  readonly type = ImagingActionTypes.GetEmployee;
  constructor(public payload: any) {}
}
export class GetEmployeeSuccess implements Action {
  readonly type = ImagingActionTypes.GetEmployeeSuccess;
  constructor(public payload: any) {}
}

// dynamic template
export class GetTemplateColumns implements Action {
  readonly type = ImagingActionTypes.GetTemplateColumns;
  constructor(public payload: any) {}
}
export class GetTemplateColumnSuccess implements Action {
  readonly type = ImagingActionTypes.GetTemplateSuccess;
  constructor(public payload: any) {}
}
export class GetTemplateColumnFailure implements Action {
  readonly type = ImagingActionTypes.GetTemplateFailure;
  constructor() {}
}

// test order data
export class GetTestOrderData implements Action {
  readonly type = ImagingActionTypes.GetTestOrderData;
  constructor(public payload: any) {}
}
export class GetTestOrderDataSuccess implements Action {
  readonly type = ImagingActionTypes.GetTestOrderDataSuccess;
  constructor(public payload: TestOrder[]) {}
}

// test image paths
export class GetTestImagePaths implements Action {
  readonly type = ImagingActionTypes.GetTestImagePaths;
  constructor(public payload: any) {}
}
export class GetTestImagePathsSuccess implements Action {
  readonly type = ImagingActionTypes.GetTestImagePathsSuccess;
  constructor(public payload: any) {}
}
export class GetTestImagePathsFailure implements Action {
  readonly type = ImagingActionTypes.GetTestImagePathsFailure;
}

// update test order
export class UpdateTestOrderData implements Action {
  readonly type = ImagingActionTypes.UpdateTestOrderData;
  constructor(public payload: any) {}
}
export class UpdateTestOrderDataSuccess implements Action {
  readonly type = ImagingActionTypes.UpdateTestOrderDataSuccess;
  constructor(public payload: any) {}
}

// image paths
export class SaveImagePaths implements Action {
  readonly type = ImagingActionTypes.SaveImagePaths;
  constructor(public payload: any) {}
}
export class SaveImagePathsSuccess implements Action {
  readonly type = ImagingActionTypes.SaveImagPathsSuccess;
  constructor(public payload: any) {}
}
export class SaveImagePathsError implements Action {
  readonly type = ImagingActionTypes.SaveImagePathsError;
  constructor(public payload: any) {}
}

export class ImagingApiError implements Action {
  readonly type = ImagingActionTypes.ApiError;
  constructor(public payload: any) {}
}

// current saved image order
export class SaveCurrentSelectedTestOrder implements Action {
  readonly type = ImagingActionTypes.SaveCurrentSelectedTestOrder;
  constructor(public payload: IPatientData & OrderEntryRes) {}
}
export class UpdateCurrentSelectedTestOrder implements Action {
  readonly type = ImagingActionTypes.UpdateCurrentSelectedTestOrder;
  constructor(public payload: IPatientData & OrderEntryRes) {}
}

export class SaveFilterState implements Action {
  readonly type = ImagingActionTypes.SaveFilterState;
  constructor(public payload: FilterFormData) {}
}

// result entry form data submission

export class SaveResultEntryFormData implements Action {
  readonly type = ImagingActionTypes.SaveResultEntryFormData;
  constructor(public payload: any) {}
}
export class SaveResultEntryFormDataSuccess implements Action {
  readonly type = ImagingActionTypes.SaveResultEntryFormDataSuccess;
  constructor(public payload?: any) {}
}

// given by in result entry screen
export class GetGivenBy implements Action {
  readonly type = ImagingActionTypes.GetGivenBy;
  constructor(public payload: any) {}
}
export class GetGivenBySuccess implements Action {
  readonly type = ImagingActionTypes.GetGivenBySuccess;
  constructor(public payload: any) {}
}

// get drug routes
export class GetDrugRoutes implements Action {
  readonly type = ImagingActionTypes.GetDrugRoutes;
  constructor(public payload: any) {}
}
export class GetDrugRouteSuccess implements Action {
  readonly type = ImagingActionTypes.GetDrugRouteSuccess;
  constructor(public payload: any) {}
}

export class GetImagingChartData implements Action {
  readonly type = ImagingActionTypes.GetImagingChartData;
  constructor(public payload: any) {}
}
export class GetImagingChartDataSuccess implements Action {
  readonly type = ImagingActionTypes.GetImagingChartDataSuccess;
  constructor(public payload: ImagingChartData[]) {}
}
export class GetImagingChartDataError implements Action {
  readonly type = ImagingActionTypes.GetImagingChartDataError;
  constructor(public payload: any) {}
}

/**
 * GetTemplateColumnsData Action
 */
export class GetTemplateColumnsData implements Action {
  readonly type = ImagingActionTypes.GetTemplateColumnsData;
  constructor(public payload: any) {}
}

/**
 * Class representing GetTemplateColumnsData Action
 */
export class GetTemplateColumnsDataSuccess implements Action {
  readonly type = ImagingActionTypes.GetTemplateColumnsDataSuccess;
  /**
   * creates a GetTemplateColumnsDataSuccess action
   * @param {any} payload action payload
   */
  constructor(public payload: any) {}
}
/**
 * Class representing GetTemplateColumnsDataError Action
 */
export class GetTemplateColumnsDataError implements Action {
  readonly type = ImagingActionTypes.GetTemplateColumnsDataError;
  /**
   * creates a GetTemplateColumnsDataError action
   * @param {any} payload action payload
   */
  constructor(public payload: any) {}
}

export type ImagingActions =
  | GetTestOrderData
  | GetTestOrderDataSuccess
  | UpdateTestOrderData
  | ImagingApiError
  | GetEmployeeSuccess
  | SaveImagePaths
  | SaveImagePathsError
  | SaveCurrentSelectedTestOrder
  | UpdateCurrentSelectedTestOrder
  | SaveFilterState
  | GetTemplateColumns
  | GetTemplateColumnSuccess
  | GetTemplateColumnFailure
  | GetTestImagePaths
  | GetTestImagePathsSuccess
  | GetTestImagePathsFailure
  | SaveResultEntryFormData
  | SaveResultEntryFormDataSuccess
  | GetGivenBy
  | GetGivenBySuccess
  | GetDrugRoutes
  | GetDrugRouteSuccess
  | UpdateTestOrderDataSuccess
  | GetImagingChartData
  | GetImagingChartDataError
  | GetImagingChartDataSuccess
  | GetTemplateColumnsDataSuccess
  | GetTemplateColumnsDataError
  | GetTemplateColumnsData;
