import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { Store, select } from '@ngrx/store';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import { Observable, Subscription } from 'rxjs';
import { TestOrder } from '@app/modules/imaging/models';
import { getSelectedTestOrder } from '@app/modules/imaging/reducers';
import { UserSession } from '@core/models/user-session';
import { ROLES_TOKEN } from '@app/modules/imaging/services/roles-constants.service';
import { ImagingService } from '@app/modules/imaging/services/imaging.service';
import { UserService } from '@core/services/user.service';
import { ValidUserGuard } from '../../services/valid-user-guard.service';
import { Router } from '@angular/router';
import { tap, filter } from 'rxjs/operators';

@AutoUnsubscribe()
@Component({
  templateUrl: './result-entry-wrapper.component.html',
  styleUrls: ['./result-entry-wrapper.component.scss']
})
export class ResultWrapperComponent implements OnInit, OnDestroy {
  constructor(
    store: Store<any>,
    userService: UserService,
    private router: Router,
    private imagingService: ImagingService,
    private validUserGuard: ValidUserGuard,
    @Inject(ROLES_TOKEN) private roles: Map<string, number>
  ) {
    this.sessionData = userService.getUser();
    this.selectedTestOrder$ = store.pipe(select(getSelectedTestOrder));
  }

  pastOrdersVisible = false;
  patientData: any;

  private selectedTestOrder$: Observable<TestOrder>;
  private selectedTestOrderSubscription: Subscription;
  private sessionData: UserSession;
  private readonly roleMap: Map<string, number> = this.roles;

  /**
   * @param requiredRoles an array of roles a user needs to have to access an item
   * @param userRoles an array of roles which a user has
   * @returns {boolean} a boolean value to let know if an user is allowed to access a resource
   */
  allowToView(requiredRoles: Array<string>, userRole: string): boolean {
    if (requiredRoles.indexOf(userRole) !== -1) {
      return true;
    } else {
      return false;
    }
  }

  checkIfUserIsValid() {
    const isValidUser = this.validUserGuard.isValidUser();
    if (!isValidUser) {
      this.router.navigate(['no-access']);
    }
  }


  ngOnInit() {
    this.checkIfUserIsValid();
    this.selectedTestOrderSubscription = this.selectedTestOrder$
      .pipe(
        tap((selectedTest) => {
          if (!selectedTest) {
            this.router.navigate(['imaging', 'test-order']);
          }
        }),
      )
      .subscribe(
        data => (this.patientData = data)
      );
  }

  ngOnDestroy() {}

  togglePastOrders() {
    this.pastOrdersVisible = !this.pastOrdersVisible;
  }

  get userDesignations(): Array<string> {
    // designation list is of the form { designatin: string, id: number }
    // but I am only interested in having an array of designation string
    // so I mapped it.
    return this.sessionData.designationsList.map(
      desigObj => desigObj.designation
    );
  }

  get userHighestRole() {
    return this.imagingService.getUserHighestRole(
      this.roleMap,
      this.userDesignations
    );
  }
}
