import {
  ImagingActions,
  ImagingActionTypes
} from '@app/modules/imaging/actions';
import { Employee } from '@app/modules/imaging/models';

const initialState: Employee[] = [];

export function reducer(state = initialState, action: ImagingActions) {
  switch (action.type) {
    case ImagingActionTypes.GetEmployeeSuccess: {
      return [...action.payload];
    }
    default:
      return state;
  }
}
