import {
  ImagingActions,
  ImagingActionTypes
} from '@app/modules/imaging/actions';

const initialState = {
  isLoading: false,
  errorOccurred: false,
  testTemplates: {}
};

export function reducer(state = initialState, action: ImagingActions) {
  switch (action.type) {
    case ImagingActionTypes.GetTemplateSuccess: {
      return {
        isLoading: false,
        errorOccurred: false,
        testTemplates: Object.assign({}, action.payload)
      };
    }
    case ImagingActionTypes.GetTemplateColumns: {
      return {
        isLoading: true,
        errorOccurred: false,
        testTemplates: {}
      };
    }
    case ImagingActionTypes.GetTemplateFailure: {
      return {
        isLoading: false,
        errorOccurred: true,
        testTemplates: {}
      };
    }
    default:
      return state;
  }
}
