import {
  Component,
  Input,
  SimpleChanges,
  OnChanges,
  EventEmitter,
  Output
} from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  Validators,
  ValidationErrors,
  NgForm
} from '@angular/forms';
import { DynamicFormFieldConfig } from '@app/modules/imaging/models';

const validationFunctionMap = new Map<string, ValidationErrors>().set(
  'mandatory',
  Validators.required
);

@Component({
  templateUrl: './dynamic-form.component.html',
  styleUrls: ['./dynamic-form.component.scss'],
  selector: 'app-dynamic-form'
})
export class DynamicFormComponent implements OnChanges {
  @Input() config: Array<DynamicFormFieldConfig>;
  @Output() formAvailable = new EventEmitter<FormGroup>();
  @Input() parentNgForm: NgForm;

  form: FormGroup;

  constructor(private fb: FormBuilder) {
    this.form = this.fb.group({});
  }

  /**
   * Creates a dynamic form using the metadata passed through input binding
   */
  createForm() {
    if (this.config.length) {
      this.config.forEach(fieldMetaData => {
        fieldMetaData.parentNgForm = this.parentNgForm;
        const rules = fieldMetaData.validationRule
          .split(';')
          .map(val => val.trim())
          .filter(val => val)
          .map(
            rule => rule.split(':').map(val => val.trim()) as [string, string]
          );
        const validationMap = new Map(rules);
        const validationFunctions = [];
        validationMap.forEach((value, key) => {
          if (validationFunctionMap.has(key) && value === 'true') {
            validationFunctions.push(validationFunctionMap.get(key));
          }
        });
        this.form.addControl(
          fieldMetaData.columnName,
          this.fb.control(null, validationFunctions)
        );
      });
      this.formAvailable.emit(this.form);
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.config && changes.config.currentValue) {
      this.config = [...this.config];
      this.createForm();
    }
  }
}
