import {
  ImagingActions,
  ImagingActionTypes
} from '@app/modules/imaging/actions';
import { ImagingChartData } from '@app/modules/imaging/models/imaging-chart-data';
const cloneDeep = require('lodash/cloneDeep');

export interface State {
  isLoading: boolean;
  data: ImagingChartData;
  errorOccurred: boolean;
}

const initialState: State = {
  isLoading: false,
  data: {
    forReview: [],
    published: [],
    revied: [],
    testDone: [],
    testDue: []
  },
  errorOccurred: false
};

export function reducer(state = initialState, action: ImagingActions) {
  switch (action.type) {
    case ImagingActionTypes.GetImagingChartData: {
      return {
        isLoading: true,
        errorOccurred: false,
        data: cloneDeep(initialState.data)
      };
    }
    case ImagingActionTypes.GetImagingChartDataError: {
      return {
        isLoading: false,
        errorOccurred: true,
        data: cloneDeep(initialState.data)
      };
    }
    case ImagingActionTypes.GetImagingChartDataSuccess: {
      return {
        isLoading: false,
        data: cloneDeep(action.payload),
        errorOccurred: false
      };
    }
    default:
      return state;
  }
}
