import {
  Directive,
  Renderer2,
  ElementRef,
  OnInit,
  OnDestroy,
  Output,
  EventEmitter
} from '@angular/core';

@Directive({
  selector: '[appFileSelector]'
})
export class FileSelectorDirective implements OnInit {
  @Output('appFileSelector')
  files: EventEmitter<FileList> = new EventEmitter<FileList>();

  constructor(private renderer: Renderer2, private element: ElementRef) {}

  ngOnInit() {
    this.attachOnChangeListener();
  }

  /**
   * Attaches on change listener on file input
   */
  attachOnChangeListener() {
    this.renderer.listen(
      this.element.nativeElement,
      'change',
      this.changeListener.bind(this)
    );
  }

  /**
   * OnChanage event handler
   * @param event
   */
  changeListener(event: any) {
    this.files.emit(event.target.files);
    this.renderer.setProperty(this.element.nativeElement, 'value', '');
  }
}
