import {
  ImagingActions,
  ImagingActionTypes
} from '@app/modules/imaging/actions';
import { TestOrder } from '@app/modules/imaging/models';
const cloneDeep = require('lodash/cloneDeep');

const initialState: TestOrder[] = [];

export function reducer(state = initialState, action: ImagingActions) {
  switch (action.type) {
    case ImagingActionTypes.GetTestOrderData: {
      return [];
    }
    case ImagingActionTypes.GetTestOrderDataSuccess: {
      return [...action.payload];
    }
    case ImagingActionTypes.UpdateTestOrderDataSuccess: {
      const [updatePaylad] = action.payload;
      const clonedState = cloneDeep(state); // 🤔 didn't found any alternative to angular.copy
      // please don't get scared by it 😀
      for (let i = 0, j = clonedState.length; i < j; i++) {
        for (
          let k = 0, l = clonedState[i].orderEntryResList.length;
          k < l;
          k++
        ) {
          if (
            clonedState[i].orderEntryResList[k].analysisRequestTestId ===
            updatePaylad.analysisRequestTestId
          ) {
            const { orgId, ...rest } = <any>{
              ...clonedState[i].orderEntryResList[k],
              ...updatePaylad
            };
            clonedState[i].orderEntryResList[k] = rest;
            return clonedState;
          }
        }
      }
      return state;
    }
    default:
      return state;
  }
}
