import {
  ImagingActions,
  ImagingActionTypes
} from '@app/modules/imaging/actions';
const cloneDeep = require('lodash/cloneDeep');

interface TemplateData {
  orgId?: string;
  data?: Array<any>;
  templateId?: 2;
  orderBy?: any;
  order?: any;
  deptId?: number;
  orgGroupId?: number;
  authenticatedUserId?: string;
  createdUserId?: number;
  patientId?: number;
  hosPatientId?: string;
  patientVisitId?: number;
  testStatus?: string;
  columnName?: string;
  analysisRequestTestId?: number;
  insertImagingContrastReq?: boolean;
  testRemarks?: string;
  doctorTestRemarks?: string;
  technicianTestRemarks?: string;
  testContrastId?: number;
  imagingConstrastResponse?: any;
  doctor?: boolean;
}

interface State {
  isLoading: boolean;
  data: TemplateData;
  errorOccurred: boolean;
}

const initialState: State = {
  isLoading: false,
  data: null,
  errorOccurred: false
};

export function reducer(state = initialState, action: ImagingActions) {
  switch (action.type) {
    case ImagingActionTypes.GetTemplateColumnsData: {
      return { isLoading: true, data: null, errorOccurred: false };
    }
    case ImagingActionTypes.GetTemplateColumnsDataSuccess: {
      return {
        isLoading: false,
        data: cloneDeep(action.payload),
        errorOccurred: false
      };
    }
    case ImagingActionTypes.GetTemplateColumnsDataError: {
      console.log('error');
      return { isLoading: false, data: null, errorOccurred: true };
    }
    default:
      return state;
  }
}
