import {
  Component,
  ViewChild,
  ElementRef,
  ExistingProvider,
  forwardRef,
  OnInit,
  ViewEncapsulation
} from '@angular/core';
import {
  ControlValueAccessor,
  NG_VALUE_ACCESSOR,
  FormControl
} from '@angular/forms';

export const CHECKBOX_INPUT_CONTROL_VALUE_ACCESSOR: ExistingProvider = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => CheckBoxComponent),
  multi: true
};

@Component({
  selector: 'app-checkbox',
  template: `
    <label class="checkcontainer txt-light-green">
      <ng-content></ng-content>
      <input type="checkbox" class="checkcontainer__checkbox" [formControl]="control" (change)="changeListener()"/>
      <span class="checkcontainer__checkmark"></span>
    </label>
  `,
  styleUrls: ['./checkbox.component.scss'],
  providers: [CHECKBOX_INPUT_CONTROL_VALUE_ACCESSOR]
})
export class CheckBoxComponent implements ControlValueAccessor, OnInit {
  @ViewChild('checkbox') checkboxElement: ElementRef;
  _onChange: (_: any) => void;
  _onTouched: (_: any) => void;
  control: FormControl;

  changeListener() {
    this._onChange(this.control.value);
  }

  ngOnInit() {
    this.control = new FormControl(false);
  }

  registerOnChange(fn) {
    this._onChange = fn;
  }

  registerOnTouched(fn) {
    this._onTouched = fn;
  }

  writeValue(value) {
    this.control.setValue(value);
  }
}
