import {
  Component,
  OnInit,
  OnDestroy,
  ChangeDetectorRef,
  Output,
  EventEmitter
} from '@angular/core';
import { Store, select } from '@ngrx/store';
import { Observable, Subscription } from 'rxjs';

import {
  getSelectedTestOrder,
  getTestOrders,
  getFilterFormState
} from '@app/modules/imaging/reducers';
import { AutoUnsubscribe } from 'ngx-auto-unsubscribe';
import {
  GetTestOrderData,
  UpdateCurrentSelectedTestOrder
} from '@app/modules/imaging/actions';
import { UserSession } from '@core/models/user-session';
import { UserService } from '@core/services/user.service';
import { FilterFormData } from '@app/modules/imaging/models';
import { map, tap, switchMap } from 'rxjs/operators';
import { DatePipe } from '@angular/common';

@AutoUnsubscribe()
@Component({
  selector: 'app-past-orders',
  templateUrl: './past-orders.component.html',
  styleUrls: ['./past-orders.component.scss']
})
export class PastOrdersComponent implements OnInit, OnDestroy {
  @Output() doEdit = new EventEmitter();

  analysisRequestId: number;
  selectedTestOrderLists = [];

  private filterState$: Observable<any>;
  private selectedOrder$: Observable<any>;
  private selectedTestOrderListSubscription: Subscription;
  private sessionData: UserSession;
  private testOrders$: Observable<any>;
  private testOrdersSubscription: Subscription;

  constructor(
    private store: Store<any>,
    userService: UserService,
    private datePipe: DatePipe,
    private cdRef: ChangeDetectorRef
  ) {
    cdRef.detach();
    this.filterState$ = store.pipe(select(getFilterFormState));
    this.selectedOrder$ = store.pipe(select(getSelectedTestOrder));
    this.testOrders$ = store.pipe(select(getTestOrders));
    this.sessionData = userService.getUser();
  }

  gotoToResultEntryScreen(test) {
    this.store.dispatch(new UpdateCurrentSelectedTestOrder(test));
    this.doEdit.emit();
  }

  ngOnInit() {
    this.selectedTestOrderListSubscription = this.selectedOrder$
      .pipe(
        tap(data => (this.analysisRequestId = data.analysisRequestId)),
        switchMap(data => {
          return this.filterState$.pipe(
            map((filterState: FilterFormData) => ({
              analysisRequestId: data.analysisRequestId,
              createdUsrId: this.sessionData.regId,
              orgId: this.sessionData.orgId,
              fromDate: this.datePipe.transform(
                filterState.fromDate,
                'yyyy-MM-dd'
              ),
              toDate: this.datePipe.transform(filterState.toDate, 'yyyy-MM-dd'),
              imagingVerifyDocorId: filterState.doctor,
              testStatus: filterState.testStatus,
              imagingTechnicianId: filterState.technician
            }))
          );
        })
      )
      .subscribe(data => this.store.dispatch(new GetTestOrderData(data)));

    this.testOrdersSubscription = this.testOrders$.subscribe(data => {
      const [testOrder] = data;
      if (testOrder && testOrder.orderEntryResList) {
        this.selectedTestOrderLists = testOrder.orderEntryResList
          .sort((a, b) => {
            return b.testPlanDttm - a.testPlanDttm;
          })
          .map(order => {
            order.orderId = this.analysisRequestId;
            return order;
          });
        this.cdRef.detectChanges();
      }
    });
  }

  ngOnDestroy() {}
}
