import {
  Component,
  Input,
  OnChanges,
  SimpleChanges,
  ChangeDetectionStrategy
} from '@angular/core';

@Component({
  selector: 'app-profile-card',
  templateUrl: './profile.card.component.html',
  styleUrls: ['./profile-card.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ProfileCardComponent implements OnChanges {
  @Input() patientData: any;

  get defaultProfileImage() {
    return 'https://s3.ap-south-1.amazonaws.com/dru.assets/images/app/icons/user-male.png';
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.patientData && changes.patientData.currentValue) {
      this.patientData = Object.assign({}, this.patientData);
    }
  }
}
