import {
  ImagingActions,
  ImagingActionTypes
} from '@app/modules/imaging/actions';
import { OrderEntryRes, IPatientData } from '../models';

const initialState: (IPatientData & OrderEntryRes) = {};

export function reducer(state = initialState, action: ImagingActions) {
  switch (action.type) {
    case ImagingActionTypes.SaveCurrentSelectedTestOrder: {
      return Object.assign({}, action.payload);
    }
    case ImagingActionTypes.UpdateCurrentSelectedTestOrder: {
      return Object.assign({}, state, action.payload);
    }
    default:
      return state;
  }
}
